<!-- 用户登录界面 -->
<template>
  <div id="login">
    <div class="bg"></div>
    <el-row class="main-container">
      <el-col :lg="8" :xs="16" :md="10" :span="10">
        <div class="bottom">
          <div class="bg-color"></div>
          <div class="container">
            <el-page-header
              @back="goBack"
              title="返回登录"
              class="back"
            ></el-page-header>
            <el-form
              :label-position="labelPosition"
              label-width="80px"
              ref="formLabelAlign"
              :model="formLabelAlign"
              :rules="rules"
            >
              <el-form-item
                label="账号"
                prop="teacherId"
                :label-width="formLabelWidth"
              >
                <el-input
                  v-model="formLabelAlign.teacherId"
                  placeholder="请输入账号"
                  @blur="checkId()"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="身份证号"
                prop="cardId"
                :label-width="formLabelWidth"
              >
                <el-input
                  v-model="formLabelAlign.cardId"
                  placeholder="请输入身份证号"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="新密码"
                prop="pwd"
                :label-width="formLabelWidth"
              >
                <el-input
                  v-model="formLabelAlign.pwd"
                  placeholder="请输入密码"
                  type="password"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="确认密码"
                prop="confirmPwd"
                :label-width="formLabelWidth"
              >
                <el-input
                  v-model="formLabelAlign.confirmPwd"
                  placeholder="请再次输入密码"
                  type="password"
                ></el-input>
              </el-form-item>
              <div class="submit">
                <el-button
                  type="primary"
                  class="row-register"
                  @click="reset('formLabelAlign')"
                  >重置</el-button
                >
              </div>
            </el-form>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row class="footer">
      <el-col>
        <p class="msg2">版权所有 ©2023 聊城大学计算机学院软工4班张龙祥
          保留所有权利
          <a href="http://gat.shandong.gov.cn/" style="color:#ff962a "
             target="_blank">鲁ICP备19001371号</a>
        </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import store from "@/vuex/store";
import { mapState } from "vuex";
export default {
  store,
  name: "login",
  data() {
    var confirm = (rule, value, callback) => {
      if (value === undefined) {
        callback(new Error("请再次输入密码"));
      } else if (value != this.formLabelAlign.pwd) {
        callback(new Error("两次输入的密码不一致"));
      } else {
        callback();
      }
    };
    return {
      labelPosition: "left",
      confirmPwd: "",
      formLabelWidth: "88px",
      formLabelAlign: {
        teacherId: "",
        pwd: "",
        cardId: ""
      },
      rules: {
        teacherId: [
          { required: true, message: "账号不能为空", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码至少为6个字符", trigger: "blur" }
        ],
        confirmPwd: [{ validator: confirm, trigger: "blur" }],
        cardId: [
          { required: true, message: "身份证号不能为空", trigger: "blur" },
          { max: 18, message: "请输入正确的身份证号", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    checkId() {
      this.$axios(`/api/tidcheck/${this.formLabelAlign.teacherId}`)
        .then(res => {
          if (res.data.code == 404) {
            this.$message({
              showClose: true,
              type: "error",
              message: res.data.message
            });
          }
        })
        .catch(error => {});
    },
    reset(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios({
            url: `/api/treset`,
            method: "post",
            data: {
              ...this.formLabelAlign
            }
          }).then(res => {
            console.log(res);
            if (res.data.code == 404) {
              this.$message({
                showClose: true,
                type: "error",
                message: res.data.message
              });
            } else {
              this.$message({
                showClose: true,
                type: "success",
                message: res.data.message
              });
              this.$router.push({
                name: "login",
                params: {
                  username: this.formLabelAlign.teacherId
                }
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    goBack() {
      this.$router.push("/");
    }
  },
  computed: mapState(["userInfo"]),
  mounted() {}
};
</script>

<style lang="scss" scoped>
.container {
  margin-top: 15px;
  margin-bottom: 32px;
}
.container .el-radio-group {
  margin: 30px 0px;
}
a:link {
  color: #ff962a;
  text-decoration: none;
}
#login {
  font-size: 14px;
  color: #000;
  background-color: #fff;
}
#login .bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow-y: auto;
  height: 100%;
  background: url("../../assets/img/login-bg.jpg") center top / cover no-repeat;
  background-color: #b6bccdd1 !important;
}
#login .main-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
#login .bottom {
  display: flex;
  justify-content: center;
  background-color: #fff;
  background-color: transparent !important;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 1;
  position: relative;
  margin-top: 120px;
}
#login .back {
  margin-top: 5px;
  margin-bottom: 10px;
  color: #000;
  height: 30px;
  font-size: 30px;
}
.bg-color {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f1f1f1;
  opacity: 0.3;
  z-index: -1;
}
#login .bottom .title {
  text-align: center;
  font-size: 30px;
}
.bottom .submit .row-register {
  width: 100%;
  background-color: #ff962a;
  border-color: #ff962a;
  margin: 10px 0px 10px 0px;
  padding: 15px 20px;
}
.bottom .submit {
  display: flex;
  justify-content: center;
}
.footer {
  margin-top: 46px;
  text-align: center;
}
.footer .msg1 {
  font-size: 18px;
  color: #fff;
  margin-bottom: 15px;
}
.footer .msg2 {
  font-size: 14px;
  color: #e3e3e3;
  margin-top: 60px;
}
/deep/ .el-form--label-left .el-form-item__label {
  text-align: right;
  font-weight: 550;
  font-size: 16px;
  color: #000;
}
/deep/ .el-page-header__title {
  font-size: 16px;
  font-weight: 550;
  color: #ff962a;
}
/deep/ .el-page-header__left {
  color: #ff962a;
  height: 24px;
}
/deep/ .el-form-item__error {
  color: #ff962a;
  font-size: 12px;
  font-weight: 550;
}
</style>



